.toggleBtn{
    display: flex;
    position: relative;
    margin: auto 0;
    flex: 2;
    cursor: pointer;
    justify-content: flex-end;
}


.toggle-container {
    width: 50px;
    height: 24px;
    border-radius: 30px;
    background-color: rgb(233,233,234);
}
.toggle--checked {
    background-color: grey;
    transition : 0.5s
}

.toggle-circle {
    position: absolute;
    top: 1px;
    right: 1px;
    width: 22px;
    height: 22px;
    border-radius: 50%;
    background-color: rgb(255,254,255);
    transition : 0.5s
}
.toggle--checked {
    right: 27px;
    transition : 0.5s
}

.toggle-container, .toggle-circle {
    justify-content: flex-end; /* 여기에 추가 */
}