.windowHeader {
    width: 100%;
    height: 2.5rem;
    background-color: grey;
    border-radius: 0.5rem 0.5rem 0 0;
    position: relative;
}
.windowHeader span {
    font-size: 1.5rem;
    color: white;
    opacity: 0.7;
    justify-items: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

#exitBtn {
    border-radius: 50%;
    background-color: tomato;
    width: 1.2rem;
    height: 1.2rem;
    position: absolute;
    top: 50%;
    left: 1.5rem;
    transform: translate(-50%, -50%);
    pointer-events: all;
    cursor: pointer;
}

#outsideContainer {
    border-radius: 0.7rem;
    background-color: darkslategray;
    order: 1;
    height: 80%;
    width: 80%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    transition: all 0.7s cubic-bezier(0.1, 0.8, 0.5, 0.9);
    will-change: transform; /* will-change 속성 추가 */
    transform-origin: center left; /* 중심 축을 왼쪽으로 조정 */
    pointer-events: none;
}

#outsideContainer.aboutMe,
#outsideContainer.project,
#outsideContainer.contactMe {
    width: 17rem;
    height: 17rem;
    margin: 0 0 0 0.5rem;
    font-size: 0.5rem;
    border-radius: 0.7rem;
    background-color: darkslategray;
    display: flex;
    position: absolute;
    top: 15%;
    flex-direction: column;
    transform: perspective(1500px) rotateY(-45deg) scale(0.7);
    will-change: transform;
    transform-origin: center;
    transition: all 0.7s cubic-bezier(0.1, 0.8, 0.5, 0.9);
    pointer-events: all;
}
#outsideContainer.project {
    top: 40%;
}
#outsideContainer.contactMe {
    top: 65%;
}

.insideContainer {
    background-color: whitesmoke;
    border-radius: 0.7rem;
    margin: 0.5rem;
    flex: 1;
    position: relative;
    overflow: hidden;
    height: 100%;
    max-height: 100%;
    overflow: auto;
    pointer-events: all;
}
.myImage img {
    width: 90%;
    border-radius: 100%;
    margin: 0 important;
}

.aboutMeContainer {
    padding: 3%;
    display: grid;
    grid-template-columns: 1fr 3fr;
    grid-template-rows: 1fr 3fr;
    grid-template-areas:
        'header detail'
        'info detail';
}

.line {
    width: 70%;
    margin: 0 auto;
    border: none;
    border-top: 0.1rem solid grey;
    opacity: 1;
}
.line2 {
    width: 90%;
    margin: 5% auto;
    border: none;
    border-top: 0.1rem solid grey;
    opacity: 1;
}

.myImage {
    grid-area: header;
    text-align: center;
}

.myInfo {
    grid-area: info;
    text-align: center;
}
.myName,
.myContact,
.myEdu {
    margin: 7% 0;
}
.myInfo span {
    font-size: 130%;
    font-weight: bold;
}
.myInfo p {
    margin-top: 5%;
}

.detailInfo {
    grid-area: detail;
    padding: 0 5%;
    overflow: auto;
}

.detailText {
    font-size: 200%;
    letter-spacing: 0.05em; /* 글자 간격 조절 */
    line-height: 1.4; /* 줄 간격 조절 */
    text-shadow: 0.1rem 0.1rem 0.2rem darkgrey; /* 적은 정도의 그림자 효과 추가 */
}

.skillsContainer {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 5%;
    text-align: center;
    overflow: auto;
}

.skills {
    background-color: white;
    display: flex;
    flex-direction: column;
    font-size: 130%;
    padding-top: 10%;
}
.skills img {
    border-radius: 0;
    width: 40%;
    margin: 20% auto;
}
