
#navbarContainer {
    border-radius: 1.3rem;
    align-self: center;
    background-color: rgba(0, 0, 0, 0.3);
    padding: 0.3rem;
    order: 2;
    /* transform: perspective(1000px) rotateY(-30deg); 기울게하기*/
}
#navbarContainer img {
    width: 4rem;
    margin: 0 0.3rem;
    transition: all 0.3s;
}
#navbarContainer img:hover {
    translate: 0 -8%;
}